    font-family: "8bit";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

/* Page Structure */
h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

body {
    min-width: 100vw;
    min-height: 100vh;
    display: grid;
    grid-template-rows: 15% 5% 60% 20%;
    font-family: "8bit";
}

.header {
    background-color: #87ccf8;
    width: 100%;
    grid-row: 1;
    display: flex;
    align-items: center;
    padding-left: 20px;
    font-family: "8bit";
}

.header-title {
    color: red;
}

.pop {
    color: black;
}

.subheader {
    background-color: rgb(163, 163, 163);
    width: 100%;
    grid-row: 2;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 40px;
}

.subheader > div {
    display: flex;
    overflow: hidden;
}

#task-count {
    margin-right: 5px;
}

.content {
    grid-row: 3;
    width: 100%;
    display: grid;
    grid-template-rows: 50px;
    grid-auto-rows: 50px;
    row-gap: 15px;
    flex-direction: column;
    padding: 50px 20px;
    overflow-x: hidden;
    background-color: grey;
}

.tab--active {
    background-color: #1E9CED;
    height: 2.5rem;
}

.tab--active:focus {
    outline: none;
}

/* Tasks */
.task {
    display: grid;
    width: 85%;
    grid-template-columns: 10% 40% 50%;
    align-items: center;
    padding: 0 10px;
    font-size: 0.8rem;
    background-color: white;
}

.task--completed {
    background-color: green;
}

.task__checkbox {
    grid-column: 1;
    margin: 0;
}

.task__desc {
    grid-column: 2;
}

.task__controls {
    grid-column: 3;
    display: flex;
    justify-content: flex-end;
}

.task__title > p {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0;
    font-size: 0.5rem;
}

.task-btn {
    width: 80px;
    margin: 0 10px;
    font-size: 0.5rem;
}

/* Todo Controls */

.todo-controls {
    grid-row: 4;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 40px;
}

.add-project-btn, .add-project-icon {
    max-width: 80px;
    max-height: 80px;
    padding: 0;
    border: none;
    background-color: white;
}

.add-project-btn:focus {
    outline: none;
}

.add-btn {
    width: 70px;
    height: 70px;
}

.warning-result {
    color: red;
    font-size: 0.6rem;
}

/* --- Dialogs/Modals --- */

/* Info Modal */
.info-form-header {
    display: flex;
    justify-content: space-between;
}

.required-symbol {
    color: #1E9CED;
}

.dialog-title-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.dialog-title {
    font-size: 1.2rem;
    color: #1E9CED;
    text-decoration: underline;
}

form label {
    margin-top: 15px;
    font-size: 0.8rem;
}

#project_field {
    margin-bottom: 30px;
}

.dialog-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}

@media (min-width: 390px){
    .task {
        width: 90%;
    }
}

@media (min-width: 430px){
    .task {
        width: 95%;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,mBAAmB;IACnB,4CAAsD;AAC1D;;AAEA,mBAAmB;AACnB;IACI,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,aAAa;IACb,kCAAkC;IAClC,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,WAAW;IACX,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,oCAAoC;IACpC,WAAW;IACX,WAAW;IACX,aAAa;IACb,yBAAyB;IACzB,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,WAAW;IACX,aAAa;IACb,wBAAwB;IACxB,oBAAoB;IACpB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA,UAAU;AACV;IACI,aAAa;IACb,UAAU;IACV,kCAAkC;IAClC,mBAAmB;IACnB,eAAe;IACf,iBAAiB;IACjB,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,cAAc;IACd,SAAS;AACb;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,cAAc;IACd,iBAAiB;AACrB;;AAEA,kBAAkB;;AAElB;IACI,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,UAAU;IACV,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,iBAAiB;AACrB;;AAEA,2BAA2B;;AAE3B,eAAe;AACf;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,0BAA0B;AAC9B;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,UAAU;AACd;;AAEA;IACI;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,UAAU;IACd;AACJ","sourcesContent":["@font-face {\n    font-family: \"8bit\";\n    src: url('/src/assets/fonts/PressStart2P-Regular.ttf');\n}\n\n/* Page Structure */\nh1, h2, h3, h4, h5, h6 {\n    margin: 0;\n}\n\nbody {\n    min-width: 100vw;\n    min-height: 100vh;\n    display: grid;\n    grid-template-rows: 15% 5% 60% 20%;\n    font-family: \"8bit\";\n}\n\n.header {\n    background-